.App {
  height: 100vh;
  width: 100vw;
  font-family: inherit;
}

.page-title {
  background-color: #d3d3d3;
  line-height: 6vh;
  padding-left: 1rem;
  font-weight: bolder;
  margin: 0;
}

.body-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15vh;
}
.friends-list-table {
  width: 20%;
}

.header-row {
  display: flex;
  justify-content: space-between;
  background-color: #d3d3d3;
  padding: 0.6rem;
}

.header {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sort-header {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.sort-star-icon {
  padding: 0.3rem;
  color: #8c8c8c;
}

.add-new-friend {
  border: 1px solid #d3d3d3;
  height: 3.5vh;
  display: flex;
  justify-content: flex-start;
  padding-left: 0.7rem;
}

input {
  border: none;
  font-family: inherit;
}

input:focus {
  border: none;
  outline: none;
}

.list-of-friends {
  height: 20vh;
}

.friend-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #d3d3d3;
  padding: 0.3rem 1rem;
}

.actions {
  display: flex;
  justify-content: space-between;
}

.text {
  font-size: 0.7rem;
  padding-top: 0.4rem;
}

.star-icon {
  padding: 0.5rem;
  border: 1px solid #d3d3d3;
  cursor: pointer;
  height: 1vh;
  width: 1.3vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0.5rem;
  color: #8c8c8c;
}

.delete-icon {
  padding: 0.5rem;
  border: 1px solid #d3d3d3;
  cursor: pointer;
  height: 1vh;
  width: 1.3vw;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #8c8c8c;
}

.pagination-component {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
}
ul {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: row;
  list-style-type: none;
  padding-top: 1rem;
}
a {
  margin: 0.3rem;
  padding: 0.7rem;
  background-color: #d3d3d3;
  color: black;
  text-decoration: none;
}

.active > a  {
  color: red;
}

@media only screen and (max-width: 600px) {
  .friends-list-table {
    width: 60%;
  }

  .list-of-friends {
    height: 25vh;
  }

}

.delete-modal {
  position               : fixed;
  z-index                : 1000;
  top                    : 0;
  left                   : 0;
  right                  : 0;
  bottom                 : 0;
  display                : flex;
  align-items            : center;
  justify-content        : center;
  background-color       : rgba(0, 0, 0, 0.5);
}

/* Extra small devices (phones, 600px and down) */

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) and (min-height: 200px) {
  .friends-list-table {
    width: 40%;
  }

  .list-of-friends {
    height: 25vh;
  }
}

/* /* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) and (min-height: 580px) {
  .friends-list-table {
    width: 40%;
  }

  .list-of-friends {
    height: 25vh;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) and (min-height: 580px) {
  .friends-list-table {
    width: 40%;
  }

  .list-of-friends {
    height: 25vh;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) and (min-height: 800px) {
  .friends-list-table {
    width: 20%;
  }

  .list-of-friends {
    height: 20vh;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) and (max-height: 700px) {
  .friends-list-table {
    width: 20%;
  }

  .list-of-friends {
    height: 30vh;
  }
}